header {
  position: fixed;
  left: 0;
  top: 30px;
  width: 100%;
  z-index: 100;
  text-align: right;
  background: #ffffff;
  opacity: 1;
  visibility: visible;
  transition: all 0.6s ease 0s;
  -moz-transition: all 0.6s ease 0s;
  -webkit-transition: all 0.6s ease 0s;
  -o-transition: all 0.6s ease 0s;
  .head-top {
    padding: 30px 65px 15px 65px;
    .menu-btn {
      position: relative;
      margin: 0 auto;
      width: 26px;
      height: 20px;
      &:before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 2px;
        background: #141414;
        transition: all 0.3s ease 0s;
        -moz-transition: all 0.3s ease 0s;
        -webkit-transition: all 0.3s ease 0s;
        -o-transition: all 0.3s ease 0s;
      }
      &:after {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 2px;
        background: #141414;
        transition: all 0.3s ease 0s;
        -moz-transition: all 0.3s ease 0s;
        -webkit-transition: all 0.3s ease 0s;
        -o-transition: all 0.3s ease 0s;
        top: auto;
        bottom: 0;
      }
      span {
        position: absolute;
        top: 50%;
        left: 0;
        width: 100%;
        height: 2px;
        background: #141414;
        margin-top: -1px;
        transition: all 0.3s ease 0s;
        -moz-transition: all 0.3s ease 0s;
        -webkit-transition: all 0.3s ease 0s;
        -o-transition: all 0.3s ease 0s;
      }
      &:hover {
        &:before {
          background: #f26b38;
        }
        &:after {
          background: #f26b38;
        }
        span {
          background: #f26b38;
        }
      }
    }
    .top-menu {
      ul {
        li {
          margin-left: 50px;
          display: inline-block;
          vertical-align: top;
          transition: color 0.3s ease 0s;
          -moz-transition: color 0.3s ease 0s;
          -webkit-transition: color 0.3s ease 0s;
          -o-transition: color 0.3s ease 0s;
          a {
            display: block;
            margin-top: 12px;
          }
          a.btn {
            margin-top: 0;
          }
        }
        li.active {
          a {
            color: #f26b38;
          }
          .btn {
            color: #ffffff;
            background: #f26b38;
            border: 1px solid #f26b38;
          }
        }
      }
    }
  }
}
header.active {
  opacity: 1 !important;
  visibility: visible !important;
  .menu-btn {
    &:before {
      opacity: 0;
    }
    &:after {
      bottom: 9px !important;
      transform: rotate(-45deg);
      -webkit-transform: rotate(-45deg);
      -moz-transform: rotate(-45deg);
      -o-transform: rotate(-45deg);
    }
    span {
      transform: rotate(45deg);
      -webkit-transform: rotate(45deg);
      -moz-transform: rotate(45deg);
      -o-transform: rotate(45deg);
    }
  }
  .head-top {
    .top-menu {
      position: fixed;
      top: 100px;
      left: 50%;
      margin-left: -100px;
      width: 200px;
      height: auto;
      text-align: center;
      opacity: 1 !important;
      visibility: visible !important;
      ul {
        li {
          display: block;
          margin: 30px 0;
        }
      }
    }
  }
}
