.skills.list {
  ul {
    margin: 0 -30px;
    li {
      padding-top: 0;
      padding-bottom: 10px;
      padding-left: 30px;
      padding-right: 30px;
      display: inline-block;
      vertical-align: top;
      width: 50%;
      .name {
        padding-left: 23px;
        position: relative;
        &:before {
          content: "\f121";
          position: absolute;
          left: 0;
          top: 3px;
          display: inline-block;
          font-family: "Ionicons";
          color: #f26b38;
          speak: none;
          font-style: normal;
          font-weight: normal;
          font-variant: normal;
          text-transform: none;
          text-rendering: auto;
          line-height: 1;
          -webkit-font-smoothing: antialiased;
          -moz-osx-font-smoothing: grayscale;
        }
      }
      .progress {
        display: none;
      }
    }
  }
}
@media (max-width: 840px) {
  header {
    top: 15px;
    .head-top {
      .menu-btn {
        display: block;
      }
      .top-menu {
        opacity: 0;
        visibility: hidden;
        height: 0px;
        transition: opacity 0.3s ease 0.5s;
        -moz-transition: opacity 0.3s ease 0.5s;
        -webkit-transition: opacity 0.3s ease 0.5s;
        -o-transition: opacity 0.3s ease 0.5s;
      }
    }
  }
  .section.started {
    .started-content {
      .h-title {
        font-size: 52px;
        padding: 0 20px;
      }
      .h-title.blog_title {
        font-size: 40px;
      }
    }
  }
  .info-list {
    ul {
      li {
        width: 48%;
      }
    }
  }
  .section.resume {
    .cols {
      .col.col-md {
        &:first-child {
          margin-bottom: 120px;
        }
      }
    }
  }
  .resume-items {
    .resume-item {
      padding-right: 0px;
    }
  }
  .service-items {
    .service-item {
      width: 45%;
    }
  }
  .box-items {
    .box-item {
      width: 47%;
    }
  }
  footer {
    bottom: 15px;
    .soc {
      float: none;
      text-align: center;
      a {
        margin: 0 6px;
      }
    }
    .copy {
      display: none;
    }
  }
  .skills.circles {
    ul {
      li {
        width: 25%;
      }
    }
  }
}
@media (max-width: 960px) {
  .section {
    padding: 0 40px 120px 40px;
  }
}
@media (max-width: 480px) {
  .section {
    padding: 0 20px 120px 20px;
  }
  .section.started {
    .started-content {
      .h-title {
        font-size: 42px;
      }
      .h-title.blog_title {
        font-size: 26px;
      }
    }
  }
  .popup-box {
    width: 320px;
  }
}
@media (max-width: 400px) {
  .section {
    .content {
      .title {
        text-align: center;
      }
    }
  }
  .section.about {
    text-align: center;
    .image {
      float: none;
      text-align: center;
      img {
        width: 100px;
        height: 100px;
        border-radius: 100px;
        -moz-border-radius: 100px;
        -webkit-border-radius: 100px;
        -khtml-border-radius: 100px;
      }
    }
    .desc {
      margin-left: 0;
    }
  }
  .section.service {
    text-align: center;
  }
  .service-items {
    .service-item {
      text-align: center;
      .icon {
        float: none;
        display: inline-block;
        vertical-align: middle;
      }
      .name {
        margin: 0 0 0 2px;
        display: inline-block;
        vertical-align: middle;
      }
    }
  }
  .section.contacts {
    text-align: center;
  }
}
@media (max-width: 1060px) {
  .section.started {
    .started-content {
      .h-title.blog_title {
        font-size: 48px;
      }
    }
  }
}
@media (max-width: 580px) {
  .section.started {
    .started-content {
      .h-title {
        span {
          display: block;
        }
      }
    }
  }
  .info-list {
    ul {
      li {
        width: 100%;
      }
    }
  }
  .service-items {
    .service-item {
      width: 100%;
      margin-right: 0;
    }
  }
  .section.works {
    .filters {
      label {
        padding-bottom: 15px;
      }
      margin-bottom: 10px;
    }
  }
  .box-items {
    margin-left: 0;
    .box-item {
      width: 100%;
      margin: 0 0 3% 0;
    }
  }
  .popup-box {
    width: 440px;
  }
  .skills.dotted {
    ul {
      li {
        width: 100%;
        display: block;
      }
    }
  }
  .skills.circles {
    ul {
      li {
        width: 33.333%;
      }
    }
  }
  .skills.list {
    ul {
      li {
        display: block;
        width: 100%;
      }
    }
  }
}
@media (max-width: 600px) {
  .section.about {
    .image {
      img {
        width: 80px;
        height: 80px;
        border-radius: 80px;
        -moz-border-radius: 80px;
        -webkit-border-radius: 80px;
        -khtml-border-radius: 80px;
      }
    }
    .desc {
      margin-left: 100px;
    }
  }
}
@media (max-width: 1000px) {
  .box-items {
    .box-item {
      .desc {
        padding: 15px 15px 5px 15px;
      }
    }
  }
}
