}
* {
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
}
.clear {
  clear: both;
}
.container {
  position: relative;
  margin: 30px;
  overflow: hidden;
  background: #ffffff;
  .line {
    content: "";
    position: fixed;
    background: #f26b38;
    z-index: 105;
  }
  .line.top {
    left: 0;
    top: 0;
    width: 100%;
    height: 30px;
  }
  .line.bottom {
    left: 0;
    top: auto;
    bottom: 0;
    width: 100%;
    height: 30px;
  }
  .line.left {
    left: 0;
    top: 0;
    width: 30px;
    height: 200%;
  }
  .line.right {
    left: auto;
    right: 0;
    top: 0;
    width: 30px;
    height: 200%;
  }
}
.wrapper {
  overflow: hidden;
  position: relative;
  margin: 0 auto;
  max-width: 1060px;
}
.cols {
  position: relative;
  .col.col-sm {
    float: left;
    width: 25%;
  }
  .col.col-lg {
    float: left;
    width: 75%;
  }
  .col.col-md {
    float: left;
    width: 50%;
  }
  &:after {
    content: "";
    display: block;
    clear: both;
  }
}
