@media (min-width: 580px) {
  .glitch-effect {
    position: relative;
    &:before {
      content: attr(data-text);
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      background: #ffffff;
      clip: rect(0, 0, 0, 0);
      left: -2px;
      text-shadow: 2px 0 #f26b38;
      animation: glitch-2 2s infinite linear alternate-reverse;
    }
    &:after {
      content: attr(data-text);
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      background: #ffffff;
      clip: rect(0, 0, 0, 0);
      left: 2px;
      text-shadow: -1px 0 #f26b38;
      animation: glitch-1 2s infinite linear alternate-reverse;
    }
  }
  .glitch-effect-white {
    position: relative;
    &:before {
      content: attr(data-text);
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      background: #ffffff;
      clip: rect(0, 0, 0, 0);
      background: #f26b38;
      left: -2px;
      text-shadow: 2px 0 #f26b38;
      animation: glitch-2 2s infinite linear alternate-reverse;
    }
    &:after {
      content: attr(data-text);
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      background: #ffffff;
      clip: rect(0, 0, 0, 0);
      background: #f26b38;
      left: 2px;
      text-shadow: -1px 0 #f26b38;
      animation: glitch-1 2s infinite linear alternate-reverse;
    }
  }
}
